@mixin font-face($fontfamily, $fontname){
	@font-face {
	font-family: '#{$fontfamily}';
    src: url('../font/#{$fontname}.eot');
    src: url('../font/#{$fontname}.eot?#iefix') format('embedded-opentype'),
         url('../font/#{$fontname}.woff2') format('woff2'),
         url('../font/#{$fontname}.woff') format('woff'),
         url('../font/#{$fontname}.ttf') format('truetype'),
         url('../font/.svg##{$fontfamily}') format('svg');
    font-weight: normal;
    font-style: normal;	
	}
}
@include font-face(whitneymedium, whitney-medium-bas-webfont);
@include font-face(whitneybook, whitney-book-bas-webfont);
@include font-face(whitneylight, whitney-lightital-bas-webfont);
@include font-face(whitneylight_italic, whitney-light-bas-webfont);

$font-normal: 'whitneymedium', sans-serif;
$font-light: 'whitneylight', sans-serif;
$font-italic: 'whitneylight_italic', sans-serif;